import React, { useRef, useState, useEffect } from 'react'
import Konva from 'konva'
import { Stage, Layer } from 'react-konva'
import StickyNote from './components/StickyNote/StickyNote'
import DelightfulToolbar from './components/DelightfulToolbar/DelightfulToolbar'
import ThemeToggle from './components/ThemeToggle/ThemeToggle'
import ZoomControls from './components/ZoomControls/ZoomControls'
import EmojiPicker from './components/EmojiPicker/EmojiPicker'
import EmojiBrushOverlay from './components/EmojiBrushOverlay/EmojiBrushOverlay'
import EmojiObject from './components/EmojiObject/EmojiObject'
import IntroductionPage from './pages/IntroductionPage/IntroductionPage'
import OverviewPage from './pages/OverviewPage/OverviewPage'
import ProjectsPage from './pages/ProjectsPage/ProjectsPage'
import { styled } from '@mui/material/styles'
import Box from '@mui/material/Box'

type Tool = 'hand' | 'pen' | 'sticky' | 'emoji' | null
type BoardItem = { id: string; type: 'sticky' | 'emoji'; src?: string; x: number; y: number }
type EmojiSubMode = 'stamp' | 'wand'
type WandEmoji = {
    id: string
    src: string
    x: number
    y: number
    rotation: number
    bornAt: number
    floatAngle: number
}

const AppContainer = styled(Box)<{ scaleValue: number }>(({ theme, scaleValue }) => ({
    width: '100vw',
    height: '100vh',
    overflow: 'hidden',
    position: 'relative',
    backgroundColor: theme.palette.background.default,
    backgroundImage:
        theme.palette.mode === 'light'
            ? `url(${process.env.PUBLIC_URL}/images/dots-light.svg)`
            : `url(${process.env.PUBLIC_URL}/images/dots-dark.svg)`,
    backgroundRepeat: 'repeat',
    backgroundSize: `${20 * scaleValue}px ${20 * scaleValue}px`,

    /* Prevent iOS text selection on long-press */
    WebkitTouchCallout: 'none',
    WebkitUserSelect: 'none',
    userSelect: 'none'
}))

const PageWrapper = styled('div')<{ baseY: number; translateX: number; translateY: number }>(
    ({ baseY, translateX, translateY }) => ({
        position: 'absolute',
        top: baseY,
        left: 0,
        width: '100vw',
        height: '100vh',
        pointerEvents: 'none',
        transform: `translate(${translateX}px, ${translateY}px)`,
        transformOrigin: 'top left'
    })
)

export default function App() {
    const stageRef = useRef<Konva.Stage>(null)
    const [stageScale, setStageScale] = useState(1)
    const [stagePos, setStagePos] = useState({ x: 0, y: 0 })
    const [activeTool, setActiveTool] = useState<Tool>(null)
    const [emojiPickerOpen, setEmojiPickerOpen] = useState(false)
    const [selectedEmoji, setSelectedEmoji] = useState(
        `${process.env.PUBLIC_URL}/images/emojis/sparkling-heart.png`
    )
    const [showOverlay, setShowOverlay] = useState(false)
    const [overlayPos, setOverlayPos] = useState({ x: 0, y: 0 })
    const [emojiButtonRect, setEmojiButtonRect] = useState({ x: 0, y: 0, width: 0, height: 0 })
    const [emojiSubMode, setEmojiSubMode] = useState<EmojiSubMode>('stamp')
    const [stampEmojis] = useState([
        `${process.env.PUBLIC_URL}/images/emojis/super-happy-emoji.png`,
        `${process.env.PUBLIC_URL}/images/emojis/sunglasses-emoji.png`,
        `${process.env.PUBLIC_URL}/images/emojis/heart-face-emoji.png`,
        `${process.env.PUBLIC_URL}/images/emojis/hot-emoji.png`,
        `${process.env.PUBLIC_URL}/images/emojis/exploding-emoji.png`,
        `${process.env.PUBLIC_URL}/images/emojis/happy-emoji.png`,
        `${process.env.PUBLIC_URL}/images/emojis/cowboy-emoji.png`,
        `${process.env.PUBLIC_URL}/images/emojis/star-face-emoji.png`,
        `${process.env.PUBLIC_URL}/images/emojis/sparkling-heart.png`
    ])
    const [smileyEmojis] = useState([
        `${process.env.PUBLIC_URL}/images/emojis/super-happy-emoji.png`,
        `${process.env.PUBLIC_URL}/images/emojis/sunglasses-emoji.png`,
        `${process.env.PUBLIC_URL}/images/emojis/heart-face-emoji.png`,
        `${process.env.PUBLIC_URL}/images/emojis/hot-emoji.png`,
        `${process.env.PUBLIC_URL}/images/emojis/exploding-emoji.png`,
        `${process.env.PUBLIC_URL}/images/emojis/cash-emoji.png`,
        `${process.env.PUBLIC_URL}/images/emojis/happy-emoji.png`,
        `${process.env.PUBLIC_URL}/images/emojis/cowboy-emoji.png`,
        `${process.env.PUBLIC_URL}/images/emojis/star-face-emoji.png`
    ])
    const [objects, setObjects] = useState<BoardItem[]>([])
    const [wandEmojis, setWandEmojis] = useState<WandEmoji[]>([])
    const spawnIntervalRef = useRef<NodeJS.Timeout | null>(null)

    // 1) Prevent iOS "Copy Image" context menu
    useEffect(() => {
        const preventContextMenu = (e: Event) => e.preventDefault()
        document.addEventListener('contextmenu', preventContextMenu)
        return () => {
            document.removeEventListener('contextmenu', preventContextMenu)
        }
    }, [])

    // 2) Move wand with pointer
    const handlePointerMove = (e: Konva.KonvaEventObject<PointerEvent>) => {
        // This can help ensure iOS doesn't try anything default
        e.evt.preventDefault()
        setOverlayPos({
            x: e.evt.clientX,
            y: e.evt.clientY
        })
    }

    const clampScale = (s: number) => {
        if (s < 0.2) return 0.2
        if (s > 3) return 3
        return s
    }
    const clampStagePosition = (y: number) => {
        const minY = -2 * window.innerHeight
        if (y < minY) return minY
        if (y > 0) return 0
        return y
    }

    const handleWheel = (e: any) => {
        e.evt.preventDefault()
        if (activeTool === 'emoji' && emojiSubMode === 'wand') {
            return
        }
        if (e.evt.ctrlKey) {
            const oldScale = stageScale
            const direction = e.evt.deltaY > 0 ? 1 : -1
            const scaleBy = 1.05
            const newScaleUnclamped = direction > 0 ? oldScale / scaleBy : oldScale * scaleBy
            const newScale = clampScale(newScaleUnclamped)
            setStageScale(newScale)
        } else {
            setStagePos((prev) => {
                const newY = clampStagePosition(prev.y - e.evt.deltaY)
                return { ...prev, y: newY }
            })
        }
    }

    // 3) Start wand on pointer down
    const handlePointerDown = (e: Konva.KonvaEventObject<PointerEvent>) => {
        e.evt.preventDefault()
        if (activeTool === 'emoji' && emojiSubMode === 'wand') {
            spawnIntervalRef.current = setInterval(() => {
                const st = stageRef.current
                if (!st) return
                const pointer = st.getRelativePointerPosition()
                if (!pointer) return

                const rotation = (Math.random() - 0.5) * 40
                const floatAngle = (Math.random() - 0.5) * (Math.PI / 3)
                const newWandEmoji: WandEmoji = {
                    id: Math.random().toString(36).substring(7),
                    src: selectedEmoji,
                    x: pointer.x,
                    y: pointer.y,
                    rotation,
                    bornAt: performance.now(),
                    floatAngle
                }
                setWandEmojis((prev) => [...prev, newWandEmoji])
            }, 100)
        }
    }

    // 4) Stop wand on pointer up or leaving stage
    const stopWand = () => {
        if (spawnIntervalRef.current) {
            clearInterval(spawnIntervalRef.current)
            spawnIntervalRef.current = null
        }
    }
    const handlePointerUp = (e: Konva.KonvaEventObject<PointerEvent>) => {
        e.evt.preventDefault()
        stopWand()
    }
    const handlePointerLeave = () => {
        stopWand()
    }
    const handleTouchEnd = () => {
        stopWand()
    }

    // Stamp logic on click
    const handleStageClick = (e: Konva.KonvaEventObject<MouseEvent>) => {
        if (activeTool === 'emoji') {
            const pointer = stageRef.current?.getRelativePointerPosition()
            if (!pointer) return
            if (document.body.style.cursor.includes('stamp-cursor.png')) {
                const newObj: BoardItem = {
                    id: Date.now().toString(),
                    type: 'emoji',
                    src: selectedEmoji,
                    x: pointer.x - 20,
                    y: pointer.y - 20
                }
                setObjects((prev) => [...prev, newObj])
            }
        }
    }

    // Clean up old wand emojis
    useEffect(() => {
        let animReq: number
        const animate = () => {
            const now = performance.now()
            setWandEmojis((prev) => prev.filter((item) => now - item.bornAt < 1000))
            animReq = requestAnimationFrame(animate)
        }
        animReq = requestAnimationFrame(animate)
        return () => {
            cancelAnimationFrame(animReq)
        }
    }, [])

    const handleToolChange = (tool: Tool) => {
        setActiveTool(tool)
        if (tool === 'emoji') {
            setEmojiPickerOpen(true)
            setShowOverlay(true)
            if (emojiSubMode === 'stamp') {
                document.body.style.cursor = `url(${process.env.PUBLIC_URL}/images/stamp-cursor.png) 0 32, auto`
            } else {
                document.body.style.cursor = `url(${process.env.PUBLIC_URL}/images/wand-cursor.png) 8 8, auto`
            }
        } else {
            setEmojiPickerOpen(false)
            setShowOverlay(false)
            document.body.style.cursor = `url(${process.env.PUBLIC_URL}/images/regular-cursor.png) 16 16, auto`
        }
    }

    const handleSelectEmoji = (emoji: string) => {
        setSelectedEmoji(emoji)
        setEmojiPickerOpen(false)
        setShowOverlay(true)
    }

    return (
        <AppContainer scaleValue={stageScale}>
            <ThemeToggle />
            <ZoomControls
                scale={stageScale}
                onZoomIn={() => setStageScale((prev) => clampScale(prev + 0.1))}
                onZoomOut={() => setStageScale((prev) => clampScale(prev - 0.1))}
            />
            <DelightfulToolbar
                activeTool={activeTool}
                setActiveTool={handleToolChange}
                setEmojiButtonRect={setEmojiButtonRect}
            />
            <EmojiPicker
                visible={emojiPickerOpen}
                stampEmojis={stampEmojis}
                smileyEmojis={smileyEmojis}
                selected={selectedEmoji}
                onSelect={handleSelectEmoji}
                anchorRect={emojiButtonRect}
                subMode={emojiSubMode}
                setSubMode={(mode) => {
                    setEmojiSubMode(mode)
                    if (mode === 'stamp') {
                        document.body.style.cursor = `url(${process.env.PUBLIC_URL}/images/stamp-cursor.png) 0 32, auto`
                    } else {
                        document.body.style.cursor = `url(${process.env.PUBLIC_URL}/images/wand-cursor.png) 8 8, auto`
                    }
                }}
            />
            <EmojiBrushOverlay
                emoji={selectedEmoji}
                visible={showOverlay}
                x={overlayPos.x}
                y={overlayPos.y}
            />

            <Stage
                ref={stageRef}
                x={stagePos.x}
                y={stagePos.y}
                scaleX={stageScale}
                scaleY={stageScale}
                width={window.innerWidth}
                height={window.innerHeight}
                draggable={!(activeTool === 'emoji' && emojiSubMode === 'wand')}
                style={{
                    position: 'absolute',
                    top: 0,
                    left: 0,
                    background: 'transparent',
                    touchAction: 'none'
                }}
                onPointerDown={handlePointerDown}
                onPointerMove={handlePointerMove}
                onPointerUp={handlePointerUp}
                // no onPointerCancel at all (some iOS versions fire it on slight movement)
                onPointerLeave={handlePointerLeave}
                onTouchEnd={handleTouchEnd}
                onClick={handleStageClick}
                onWheel={handleWheel}
            >
                <Layer>
                    <StickyNote text="or else" color="#b19cd9" stageRef={stageRef} />
                    <StickyNote text="Hire me" color="#b3cde0" stageRef={stageRef} />
                    {objects.map((obj) =>
                        obj.type === 'sticky' ? null : (
                            <EmojiObject key={obj.id} src={obj.src!} x={obj.x} y={obj.y} />
                        )
                    )}
                    {wandEmojis.map((we) => {
                        const elapsed = performance.now() - we.bornAt
                        const progress = Math.min(elapsed / 1000, 1)
                        const dist = 75
                        const offsetX = dist * progress * Math.sin(we.floatAngle)
                        const offsetY = -dist * progress * Math.cos(we.floatAngle)
                        const currentX = we.x + offsetX
                        const currentY = we.y + offsetY
                        const currentOpacity = 1 - progress
                        return (
                            <EmojiObject
                                key={we.id}
                                src={we.src}
                                x={currentX}
                                y={currentY}
                                rotation={we.rotation}
                                opacity={currentOpacity}
                            />
                        )
                    })}
                </Layer>
            </Stage>

            <PageWrapper baseY={0} translateX={stagePos.x} translateY={stagePos.y}>
                <IntroductionPage />
            </PageWrapper>
            <PageWrapper baseY={window.innerHeight} translateX={stagePos.x} translateY={stagePos.y}>
                <OverviewPage />
            </PageWrapper>
            <PageWrapper
                baseY={window.innerHeight * 2}
                translateX={stagePos.x}
                translateY={stagePos.y}
            >
                <ProjectsPage />
            </PageWrapper>
        </AppContainer>
    )
}
